// adding reset
@import "reset";
//add mixins @media
@import "mixins";

body {
  height: 100vh;
  background-image: url(../assets/mainbackground.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .progress {
    margin-top: 5vh;
    height: 5vh;
    width: 600px;
    border-radius: 50px;
    background-color: rgba(7, 121, 41, 0.53);

    .progress-done {
      height: 100%;
      width: 0;
      border-radius: 50px;
      color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      background: linear-gradient(to left, rgba(255, 77, 48, 0.54), rgba(185, 1, 1, 0.53));
      opacity: 0;
      transition: 1s ease;

    }
  }

  .main-game {
    width: 1000px;
    height: 100vh;
    display: flex;
    //background-image: url(../assets/mainbackground.png);
    align-items: flex-end;
    justify-content: center;

    .troll {
      height: 900px;
      width: 900px;
      background-image: url(../assets/troll/walk/WALK000.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;

      display: flex;
      flex-direction: row;
      justify-content: center;

      .col-1 {
        margin-left: 7%;
        height: 100%;
        width: 35%;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        border: 2px solid green;

        .arm {
          margin-top: 75%;
          height: 300px;
          width: 200px;
          border: 2px solid green;
        }

      }

      .col-2 {
        padding-top: 25%;
        height: 100%;
        width: 26%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        border: 2px solid green;

        .head {
          height: 180px;

          border: 2px solid green;
        }

        .belly {
          height: 200px;

          border: 2px solid green;
        }

        .leg {
          height: 100px;

          border: 2px solid green;
        }

      }

      .col-3 {
        height: 100%;
        width: 35%;

        display: flex;
        justify-content: flex-start;
        align-items: center;

        border: 2px solid green;

        .arm {
          margin-top: 85%;
          height: 250px;
          width: 150px;

          border: 2px solid green;
        }
      }

      .blood {
        position: absolute;
        top: 60%;
        background-image: url(../assets/blood.png);
        width: 13vw;
        height: 25vh;
        background-size: cover;
        transform: rotate(26deg);


        // do zrobienia pojawiajacy sie div z krwia w roznych lokalizacjach jak trafione (js)

      }

    }

    .score {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: fixed;
      top: 2.5rem;
      left: 13rem;
      padding: 10px;

      width: 450px;
      height: 200px;


      background-image: url(../assets/score.png);
      background-size: 120%;
      background-position: center;
      background-repeat: no-repeat;

      font-size: 35px;
      font-weight: 700;

      .score-number {
        margin-top: 10%;
        width: 150px;
        height: 80px;
        text-align: center;

      }

      .info {
        padding-top: 40px;
        font-size: 16px;
        font-weight: 300;
        color: #a96a13;

      }

    }

    .viewfinder {
      position: absolute;
      width: 140px;
      height: 160px;
      background-image: url(../assets/bron.png);
      background-size: 100%;
      background-position: center;
      background-repeat: no-repeat;
    }


  }

  .buttons {
    height: 30vh;
    width: 30vw;
    position: absolute;
    border: 1px solid red;

    .btn {

      font-size: 40px;
      font-family: sans-serif;
      border-radius: 30px;
      cursor: pointer;

      &:hover {
        background-color: #77b977;
      }
    }

    .start {
      height: 80px;
      width: 200px;
      background-image: url(../assets/start.png);
      background-size: 120%;
      background-position: center;
      background-repeat: no-repeat;
      position: fixed;
      bottom: 230px;
      right: 200px;

    }

    .shot {
      height: 80px;
      width: 200px;
      background-image: url(../assets/stop.png);
      background-size: 120%;
      background-position: center;
      background-repeat: no-repeat;
      position: fixed;
      bottom: 130px;
      right: 200px;


      &:hover {
        background-image: url(../assets/start.png);
      }
    }

    .stop {
      height: 80px;
      width: 200px;
      background-image: url(../assets/stop.png);
      background-size: 120%;
      background-position: center;
      background-repeat: no-repeat;
      position: fixed;
      bottom: 30px;
      right: 200px;
    }

  }


}


@keyframes move {
  0% {
    top: 20%;
    left: 20%;
  }
  10% {
    top: 52%;
    left: 17%;
  }
  15% {
    top: 34%;
    left: 62%;
  }
  20% {
    top: 44%;
    left: 10%;
  }
  30% {
    top: 24%;
    left: 20%;
  }
  40% {
    top: 54%;
    left: 66%;
  }
  50% {
    top: 24%;
    left: 15%;
  }
  60% {
    top: 42%;
    left: 33%;
  }
  70% {
    top: 44%;
    left: 50%;
  }
  90% {
    top: 35%;
    left: 45%;
  }
  100% {
    top: 60%;
    left: 10%;
  }
}

@keyframes moveTwo {

  0% {
    transform: translate(10vh, -10vh);
  }
  20% {
    transform: translate(3vh, 10vh);
  }
  40% {
    transform: translate(17vh, 22vh);
  }
  60% {
    transform: translate(7vh, 2vh);
  }
  80% {
    transform: translate(22vh, 8vh);
  }
  100% {
    transform: translate(-10vh, 10vh);
  }

}

